@import url(https://fonts.googleapis.com/css2?family=Viga&display=swap);
html, body {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --tipo-letra-1: 'Roboto', sans-serif;
  --tipo-letra-2: 'Roboto Condensed', sans-serif;
  --tipo-letra-3: 'Montserrat', sans-serif;
  --tipo-letra-4: 'Viga', sans-serif;
  --tipo-letra-logo: 'Viga', sans-serif;

  --color-principal: #004aaa;
  --color-secundario: #5985ca;
}

* {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

#root {
  height: 100%;
}

.App {
}

.titulo-1 {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--tipo-letra-3);
  font-weight: 300;
  font-size: 2.19rem;
}

.titulo__contenedor {
  margin: 2.42em 1.45em;
}

.titulo__caption {
  color: #494848;
  font-size: 2.71rem;
}

.form__titulo {
  margin: .92em 0 1.4em;
  color: #555;
}

.form__label {
  font-size: .92rem;
  margin: .41em 0;
  color: #333;
}

.form__input {
  width: 100%;
  height: 2.4em;
  margin-bottom: 1em;
  padding: 0 .42em;
  box-sizing: border-box;
  font-size: 1.02rem;
  font-family: 'Roboto', sans-serif;
  font-family: var(--tipo-letra-1);
  resize: vertical;
  border: 2px solid #ccc;
  border-radius: 4px;
  transition: box-shadow 0.20s ease-in-out, border 0.20s ease-in-out, outline 0.20s ease-in-out;
}

.form__input:focus {
  box-shadow: 0 0 5px#785ada;
  border: 2px solid#785ada;
  outline: none;
}

.form__submit {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  box-sizing: border-box;
  padding: 0 .2em;
  margin: 1.4em 0 2em 0;
  border: none;
  border-radius: 3px;
  background: #775ada;
  color: #fff;
  font-size: .94rem;
  cursor: pointer;
}

.no-encontrado {
  font-size: 14rem;
  text-align: center;
}

.algo-ha-salido-mal {
  padding-top: 2em;
  font-size: 4rem;
  text-align: center;
}

.validacion-error {
  color: #004aaa;
  color: var(--color-principal);
}

.lista {
  position: relative;
}

.lista .lista-contenedor {
  background-color: #d6eaf7;
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
  width: 100%;
  max-height: 400px;
  border: 1px solid #d8d8d8;
  overflow-x: hidden;
  white-space: nowrap;
  transition: height 1s;
  z-index: 9999;
}

.lista .lista-contenedor.cerrado {
  border: none;
  height: 0;
}

.lista .lista-header,
.lista .lista-contenedor .lista-item {
  box-sizing: border-box;
  min-height: 2.4em;
  padding-left: .9em;
  background-color: #fff;
  color: #777;
  font-size: 1.2rem;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 2.4em;
  cursor: pointer;
  overflow-x: hidden;
  white-space: nowrap;
  transition: box-shadow .2s ease-in-out;
}

.lista .lista-header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  border-radius: 6px;
  border: 2px solid #ccc;
  overflow: hidden;
  white-space: nowrap;
}

.lista .lista-header .disponible {
  margin: 0 1.9em;
}

.lista .lista-contenedor .btn-cerrar {
  background-color: #fff;
  text-align: right;
  color: #777;
  padding: .21em .5em .21em 0;
  cursor: pointer;
}

.lista .lista-item:hover {
  background-color: #d6eaf7;
  color: #fff;
  box-shadow: inset 0px 0px 41px 0px rgba(120,90,218,0.5);
  border: none;
}

.spinner {
  margin: 0 .31em 0 .11em;
  width: 20px;
  height: 20px;
  pointer-events: none;
  box-sizing: border-box;
  border: 3px solid #ffffff7f;
  border-bottom-color: #fff;
  border-radius: 50%;
  -webkit-animation: spinner-giro 1s infinite linear ;
          animation: spinner-giro 1s infinite linear ;
}

@-webkit-keyframes spinner-giro {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}

@keyframes spinner-giro {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}

.spinner.invisible {
  display: none;
}

.width100 {
  width: 100%;
}

.height100 {
  height: 100%;
}

.border-box {
  box-sizing: border-box;
}

.of-x-hidden {
  overflow-x: hidden;
}

.a-limpio,
.a-limpio:link, 
.a-limpio:visited,
.a-limpio2,
.a-limpio2:link, 
.a-limpio2:visited {
  text-decoration: none;
}

.a-limpio,
.a-limpio:link, 
.a-limpio:visited { 
