.admin__item .admin__item_icono {
    line-height: 50%;
    margin-right: 1em;
    width: 30px;
}

/* */
.lista-items {
    width: 90%;
    margin: 0 auto;
}

.li-item {
    margin: 1em 0;
    padding: 1em;
    border: 2px solid #eee;
    border-radius: 4px;
    /* height: 50px; */
}

.li-item-datos {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.li-item__icono {
	margin-right: 2em;
	flex: 0 0 10%;
    border-radius: 2px;
    object-fit: scale-down;
}

.li-item__info {
	flex-grow: 1;
}

.li-item__nombre-articulo {
    font-weight: 600;
    margin-bottom: 6px;
}

.li-item__fecha {
	flex: 0 0 20%;
    font-size: .96rem;
}

.li-item__precio {
	flex: 0 0 14%;
    font-weight: 600;
    font-size: 1.74rem;
    color: #555;
}

.total-carrito-admin {
	display: block;
	width: 90%;
	margin: 0 auto;
	text-align: right;
	font-size: 2rem;
	font-family: var(--tipo-letra-1);
	font-weight: 500;
}

.bot-finalizar-compra {
	display: block;
	width: 200px;
	margin: 2em auto 0;
} 

.pago-carrito {
    padding: 1em;
    box-sizing: border-box;
}

.pago-carrito .bot-pagar-carrito {
    display: block;
    width: 200px;
    margin: 0 auto;
}

.pagar-carrito__miniaturas {
    margin: 2em 0;
    padding: 2em 0;
}

.pagar-carrito__miniaturas .carrito-pago-miniatura {
    margin: 0 1em;
    padding: 1em;
    width: 64px;
    height: 64px;
    object-fit: scale-down;
}

.pagar-carrito__miniaturas .carrito-pago-miniatura:hover {
    box-shadow: 0px 1px 4px 0px rgba(0,0,0,0.5);
}
.pagar-compra-view {
    margin: 1em;
    min-height: 100%;
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, auto));
}

.pagar-compra__info-articulo {
    padding: 1em;
    align-self: flex-start;
    text-align: center;
}

.pagar-compra__info-articulo__titulo-articulo {
    margin: 1.1em 0 3.2em;
    text-align: left;
}

.pagar-compra__info-articulo .img {
    max-width: 60%;
    text-align: center;
}

.pagar-compra__pago {
    flex: 1 1;
    padding: 1em;
    border-left: 1px solid #ccc;
}

.pagar-compra__pago__titulo {
    margin: 1.1em 0;
}

.pagar-compra__pago__precio-total {
    padding: 1em 0;
}

.pagar-compra__pago__precio-total .precio-total {
    font-size: 3rem;
}

.bot-finalizar-compra {
    width: 200px;
}

@media screen and (max-width: 1366px) {
    .pagar-compra__pago {
        border: none;
    }
}

@media screen and (max-width: 1024px) {
    .pagar-compra-view {
        display: block;
        width: 100%;
        margin: 0;
        padding: 1em;
    }

    .pagar-compra__info-articulo {
        width: 100%;
        text-align: center;
    }

    .pagar-compra__info-articulo,
    .pagar-compra__pago {
        padding: 0;
        border: none;
    }

    .pagar-compra__pago .campo {
        margin: 1em;
    }
}

@media screen and (max-width: 768px) {
    .pagar-compra__info-articulo {
        width: 100%;
    }
}

@media screen and (max-width: 500px) {
    .pagar-compra-view {
        width: 100%;
        margin: 0;
        padding: 1em;
    }

    .pagar-compra__info-articulo,
    .pagar-compra__pago {
        padding: 0;
        border: none;
    }

    .pagar-compra__pago .campo {
        margin: 0;
    }

    .pagar-compra__pago__precio-total .precio-total {
        font-size: 2rem;
        font-weight: 600;
    }
}
.comprar-articulo-view .marco {
    margin: 1em;
    padding: 1em;
    max-width: 100%;
}

.detalles-compra {
    width: 100%;
    display: flex;
    margin: 1em 1.5em;
    padding: 1em;
    border-radius: 6px;
}

.comprar-articulo__imagen {
    flex: 1 1;
    padding: 0 4em;
    box-sizing: border-box;
    align-self: flex-start;
}

.comprar-articulo__imagen .titulo-alternativo {
    text-align:center;
    display: none;
}

.comprar-articulo__imagen .imagen-visible {
    text-align: center;
}

.comprar-articulo__imagen .img {
    /* max-width: 100%; */
    width: 400px;
    height: 400px;
    object-fit: scale-down;
}

.comprar-articulo__imagen .img-miniatura {
    width: 64px;
    height: 64px;
    max-width: 100%;
    padding: .2em;
    box-sizing: border-box;
    object-fit: scale-down;
    cursor: pointer;
}

.comprar-articulo__imagen .img-miniatura:hover {
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.15);
}


.comprar-articulo__informacion {
    flex: 1 1;
    margin: 0 .1em;
    padding: 0 4em;
    box-sizing: border-box;
    overflow: hidden;
}

.titulo-publicacion-compra {
    margin: .4em 0;
}

.comprar-articulo__precio {
    margin: .4em 0;
    font-size: 3.56rem;
}

.comprar-articulo__informacion .art-descr {
	padding: 1em;
    max-width: inherit;

	text-overflow: ellipsis;
	white-space: pre-wrap;
    overflow-wrap: break-word;
    overflow: hidden;
    
    border: 2px dashed #ddd;
    box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.15);
}

.comprar-articulo__informacion .art-descr.colapsado {
	max-height: 200px;
}

.comprar-articulo__btn-comprar {
    display: block;
    width: 100%;
    margin: .44em 0;
    color: #fff;
    font-size: 1.53rem;
    background-color: #1f4287;
    padding: .48em .52em;
    border: none;
    border-radius: 2px;
}

.comprar-articulo__seleccionar-cantidad {
    background: none;
    border: none;
    font-size: 1.1rem;
}

.comprar-reviews-usuarios {
    width: 90%;
    margin: 0 auto;
}

.review-usuario {
    padding: 1em;
    display: inline-block;
    width: 320px;
    margin: 1em;
    text-align: left;
    border: 1px solid #c0c0c0;
    border-radius: 8px;
}

.review-usuario .review-contenido {
    align-items: flex-start;
}

.review__foto-perfil {
    margin-right: 1em;
    min-width: 60px;
    min-height: 60px;
}

.review__foto-perfil .img {
    border-radius: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.review__informacion {
    overflow: hidden;
    word-wrap: none;
    text-overflow: ellipsis;
}

@media screen and (max-width: 1200px) {
    .comprar-articulo__informacion {
        margin: 0;
        /* width: 100%; */
    }

    .review__foto-perfil {
        margin: 0;
    }
}

@media screen and (max-width: 1024px) {
    .comprar-articulo-view .marco,
    .detalles-compra {
        margin: 0;
        padding: 0;
    }

    .detalles-compra {
        display: block;
    }

    .comprar-articulo__imagen .titulo-alternativo {
	display: block;
    }
    
    .comprar-articulo__informacion, 
    .comprar-articulo__imagen {
        padding: 1em;
    }

    .comprar-articulo__informacion .titulo-publicacion-compra {
 	display: none;
    }

    .review-usuario {
        margin: 1em 0;
        width: 100%;
        border: none;
        border-radius: 1px;
        box-shadow: 0px 1px 4px 0px rgba(0,0,0,0.5);
    }
}

.vender-articulo-view {
    margin: 1em;
    padding: 1em;
    transition: height 1s;
}

.titulos-etapas {
    margin-bottom: 4em;
    padding: 1.2em 0;
    border-bottom: 2px solid #ddd;
}

.vender-articulo-titulo {
    font-size: 1.4rem;
    text-align: right;
}

.categoria-vender {
    display: inline-block;
    width: 140px;
    height: 150px;
    margin: 1em 2em;
    padding: 1em;
    border-radius: 1.12em;
    cursor: pointer;
}

.categoria-vender .subcontenedor {
    height: 100%;
    flex-direction: column;
    justify-content: space-around;
}

.categoria-vender .categoria-icono {
    color: #777;
}

.categoria-vender .categoria-icono {
    font-size: 2.34rem;
}

.categoria-vender .categoria-titulo  {
    font-size: 1.04rem;
}

.categoria-vender.seleccionado {
    background-color: var(--color-principal);
    border: none;
}

.categoria-vender.seleccionado .categoria-icono,
.categoria-vender.seleccionado .categoria-titulo {
    color: #fff;
}

.precio-cantidad-publicacion-contenedor {
    padding: 1em;
}

.descripcion-publicacion-contenedor .form__submit,
.titulo-publicacion-contenedor .form__submit,
.precio-cantidad-publicacion-contenedor .form__submit {
    width: 120px;
}

.precio-cantidad-publicacion-contenedor .form__submit {
    width: 120px;
    margin: 0 auto;
}

.precio-cantidad-publicacion__envios .form__input {
    margin: 0;
}

.vender-articulo__bot-agregar-fotos {
    padding: 2em 0 !important;
    width: 200px;
    margin: 0 auto;
    justify-content: center;
    background: linear-gradient(to right, rgb(0, 180, 219), rgb(0, 131, 176));
    border-radius: 4px;
}

.vender-articulo__bot-agregar-fotos .sp-icono,
.vender-articulo__bot-agregar-fotos .sp-label {
    display: inline-block;
    font-size: 1rem;
    line-height: 50%;
    font-weight: 500;
    margin: 0 .2em;
}

.vender-articulo__bot-agregar-fotos .sp-icono {
    font-size: 1.15rem;
}

.subir-fotos {
    margin: 4em 0;
}

.subir-fotos .limite-excedido.escondido {
    visibility: hidden;
}

.publicacion-foto {
    position: relative;
    display: inline-block;
    width: 128px;
    height: 128px;
    margin: .2em;
    border: 4px solid #ccc;
    border-radius: 4px;
}

.publicacion-foto .btn-eliminar {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999;
    color: #fff;
    font-size: 0rem;
    cursor: pointer;
    opacity: 0;
    transition: opacity .2s ease-in-out, font-size .2s ease-in-out;
}

.publicacion-foto .img {
    width: 100%;
    height: 100%;
    object-fit: scale-down;
}

.publicacion-foto:hover {
    box-shadow: 0px 0px 10px var(--color-principal);
    outline: 2px solid var(--color-principal);
}

.publicacion-foto:hover .btn-eliminar {
    font-size: 1.74rem;
    opacity: 1;
}

.finalizar-campo {
    margin: .5em .25em;
    padding: 1em;
    /* border: 2px solid #ddd; */
}

.finalizar-campo .finalizar-campo__info {
    font-size: 1.34rem;
    font-weight: 400;
    color: #333;
    border-bottom: 2px solid #ddd;
    margin-bottom: 1em;
}

.finalizar-campo .finalizar-campo__valor {
    font-size: 1.54rem;
    font-weight: 300;
    color: #333;
}

@media screen and (max-width:512px) {
    .publicacion-steeper {
        display: none;
    }
}
